.wrapper {
    background: #ECEFF1;
    height: 80%;
    width: 570px;
    display: flex;
    flex-flow: wrap;
    padding-top: 10px;
    padding-right: 10px;
    padding-left: 10px;
    margin: 0 auto;
    margin-top: 20px;
  }
  
  @media only screen and (max-width: 600px) {
  .wrapper {
    background: #ECEFF1;
    height: 50%;
    width: 400px;
    display: flex;
    flex-flow: wrap;
    padding-top: 10px;
    padding-right: 10px;
    padding-left: 10px;
    margin: 0 auto;
    margin-top: 80px;
      
    }
  }